body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html,ul{
  padding: 0;
}

.App{
  color: whitesmoke;
}

.list-container{
  width: 30%;
  /* height: 100vh; */
  background-color: #4b5970;
}

.user-container{
  width: 70%;
  background-color: #2d3542;
}


/* HELPERS */

* {
  box-sizing: content-box;
}

ul{
  list-style: none;
}

.flex{
  display: flex;
}

.flex-col{
  flex-direction: column;
}

.justify-center{
  justify-content: center;
}

.space-around{
  justify-content: space-around;
}

.space-between{
  justify-content: space-between;
}

.align-center{
  align-items: center;
}

/* ************************************** */
.user-list{
    
}

.control-panel{
    width: 100%;
}

.add-user-btn{
    width: 2.5rem;
    height: 2.5rem;
    border-radius: 50%;
    border: none;
    font-size: 2rem;
    background-color: black;
    cursor: pointer;
    color: rgb(212, 211, 211);
    transition: all 0.2s;
}

.add-user-btn:hover{
    background-color: rgb(53, 48, 48);
}
.user-preview{
    margin: 0.5rem;
    transition: all 0.4s;
    cursor: pointer;
}

.details-container{
}

.user-preview:hover{
    background-color: #354661;
}

.preview-avatar{
    width: 4rem;
    height: 4rem;
    margin: 1rem;
}

.delete-btn{
    width: 2.5rem;
    height: 2.5rem;
    border-radius: 50%;
    border: none;
    font-size: 2rem;
    background-color: rgba(0,0,0,0);
    cursor: pointer;
    color: rgb(212, 211, 211);
    transition: all 0.2s;
}

.delete-btn:hover{
    color: black
}
.user-details , .user-list{
    padding: 1rem;
}

.detail-container{
    margin: 1rem;
}

.detail-avatar{
    width: 10rem;
    height: 10rem;
}

.detail{
    font-size: 1.5rem;
}

.description{
    color: rgb(192, 192, 192);
}


